nav{
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    position: fixed;
    top: 0;
    z-index: 20;
    background-color: rgb(17, 20, 23);

}


nav ul a{
    cursor: pointer;
}
nav ul a:hover{
    color:rgb(43, 139, 139);
    border-bottom: 2px solid white;
}
.active{
    color:rgb(43, 139, 139);
    border-bottom: 2px solid white;
}



nav ul{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    color: white;
    list-style: none;
    font-size: 20px;
}
