nav h1 {
    font-size: 40px;
    background: linear-gradient(to right, rgb(0, 225, 255), white);

    background-clip: text;
    color: transparent;
}

.hamburger{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    flex-direction: column;
    display: none;
    
    
}
.ham{
    width: 50px;
    height: 2px;
    background-color: aliceblue;
    transition: all 0.5s;
}
.mobilemenu{
    position: absolute;
    top: 80px;
    width: 100%;
    height: 100vh;
    background-color:   rgba(28, 28, 29, 0.623);;
    backdrop-filter:blur(7px);
    right: 0px;
    transform: translatex(100%);
    flex-direction: column;
    transition: all 0.5s;
    gap: 40px;
}

.activemobile{
    transform: translatex(0);
   
}

.activeham .ham:nth-child(1){
    transform: rotate(45deg);
    position: relative;
    top: 8px;
    
}
.activeham .ham:nth-child(2){
   opacity: 0;
    
}
.activeham .ham:nth-child(3){
    transform: rotate(-45deg);
    position: relative;
    top: -12px;
    
}

@media (max-width:750px) {
    .desktopmenu{
        display: none;

    }
    .hamburger{
        display: flex;
    }
    
}

